






















export default {
	
	
	data(){
		return{
			baseUrl:this.api.baseUrl,
			dataList: [],
			
		}
	},
	onLoad() {
		this.loadData()
	},
	methods:{
		onitemclick(item){
			
			
			this.$eventHub.$emit('selectedBlock',item);
			
			uni.navigateBack();
		},
		
		loadData(){
				uni.request({
					url:this.api.getShopList,
					success: (res) =>  {
						console.log(res.data);
						var result = res.data;
						if(result){
							this.dataList = result.shopList	
						}else{
							uni.showToast({
								icon:"none",
								title:'数据异常'
							})
						}
					},
					fail:(error)=> {
						console.log("error :",error.errMsg);
						uni.showToast({
							icon:"none",
							title:'网络异常'
						})
					}
				})
		}
	}
	
}
